*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
.app {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
  padding-right: 5px;
}



.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: 'Times New Roman', Times, serif;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 25px -5px, rgba(0, 0, 0, 0.3) 0px 10px 10px -5px;
}

.App-link {
  color: #61dafb;
}


.card{
  box-sizing:border-box;
width: 100%;
/* height: 260px;   its getting problem to make resposive website       */
display: flex;
padding: 2%;
}
.img-block{
  width: 30%;

}
.card-image{
  width: 100%;
  border: 5px solid rgb(222, 222, 444);

}
figcaption {
  color: rgb(10, 8, 8);
  font-style: italic;
  padding: 1px;
  text-align: center;
}
.section{
  margin-left: 30px;
width: 70%;
display: flex;
flex-direction: column;
}
.sechead{
  width: 100%;
  max-width: 300px;
  display: flex;
  /* justify-content: space-around; */
  line-height:20px;
  margin-bottom: 5px;
}
.location-logo{
  width: 12px;
  height: 12px;
  display: inline-block;
  padding: 1px;
}
.map-link{
font-style: normal;
font-weight: 400;
font-size: 15.24px;
text-transform: capitalize;
padding-left: 5px;
color: #918E9B;

}
.map-link a{
  text-decoration: none;
}
.section h3{
  line-height: 30px;

color: #2B283A;
}
.section .time{
  font-style: normal;
font-weight: 700;
font-size: 15.24px;
margin: 10px 0;

color: #2B283A;
}
.desc{
font-style: normal;
font-weight: 400;
font-size: 15.24px;
color: #2B283A;
margin: 10px 0;
}
footer{
  text-align: center;
  background-color: #36525a;
  color: whitesmoke;
}

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 568px) {
  .card{
    display: flex;
    flex-direction: column;


  }
  .sechead{
    padding-top: 5px;
    padding-bottom: 5px;

  }
  .section{
    margin-left: 10px;
    width: 100%;
  }
.img-block{
  width: 100%;
}
.section h3{
  line-height: 20px;
}
.section .time{
  font-size: 12px;
  margin: 3px 0;
}
.desc{
  margin: 4px 0;
}
}
